<script>
    export default {
        props: {
            version: String
        },
        computed: {
            year() {
                return new Date().getFullYear();
            }
        }
    }
</script>

<template>
    <div class="dash-footer">
        <span v-if="version" class="version">Version {{ version }}</span>
        &copy; Copyright {{ year }} Affinity - <a href="http://www.affinityteam.co.nz/" target="affinity">Affinity Employer Services</a>
        <a href="http://www.affinityteam.co.nz/" target="affinity"><span class="dash-footer-logo"></span></a>
    </div>
</template>
