
import {defineComponent} from "vue";
import modal from "@/components/common/modal.vue";

export default defineComponent({
name: "TAndC",
  components: {modal},
  props: {
    showButton: {
      type: Boolean,
      default: true,
    }
  },

  data() {
    return {
      showModal: false
    }
  },
  methods: {
    toggleTC() {
      (this.$refs.tcModalRef as any).toggleShow();
    }
  }
})
