<script>
    import NavItem from './nav-item.vue';
    import Scrollable from '../scrollable.vue';

    export default {
        props: {
            apps: Array,
            logoutUrl: String
        },
        data() {
            return {
                mousedown: false
            }
        },
        computed: {
            enabledApps() {
                if (this.apps instanceof Array) {
                    let apps = [];

                    this.apps.forEach((app) => {
                        if (app && app.Enabled) apps.push(app);
                    })

                    return apps;
                }

                return [];
            },
            signoutUrl() {
                if (this.logoutUrl) return this.logoutUrl;

                let url = this.apps[0].Url;

                for (let i = 1; i < this.apps.length; i++) {
                    let app = this.apps[i];

                    if (app && app.Name == Affinity.app) {
                        return url + '/Root/ExternalLogout?redirectUrl=' + encodeURI(app.Url);
                    }
                }

                return '/Auth/SignOut';
            }
        },
        methods: {
            onMouseDown() {
                this.mousedown = true;
            },
            onBodyMouseDown(evt) {
                if (!this.mousedown) this.$emit('hide');
                this.mousedown = false;
            }
        },
        created() {
            document.body.addEventListener('mousedown', this.onBodyMouseDown);
        },
        beforeUnmount() {
            document.body.removeEventListener('mousedown', this.onBodyMouseDown);
        },
        components: {
            NavItem,
            Scrollable
        }
    }
</script>

<template>
    <div id="dash-nav" class="dash-nav accordion" @mousedown="onMouseDown">
        <div class="top">
            <scrollable :scroll-x="false">
                <nav-item v-for="(app, index) of enabledApps" :data="app" :key="index"></nav-item>
            </scrollable>
        </div>
        <div class="bottom">
            <div class="divider"></div>
            <a class="dash-nav-item" :href="signoutUrl">
               <span class="dash-nav-item-inner">
                   <span class="icon icon-logout"></span>
                   <span class="label">Logout</span>
               </span>
            </a>
        </div>
    </div>
</template>
