
import { defineComponent, ref } from 'vue'
import Remote from "@/remote";
import modal from "@/components/common/modal.vue";
import TAndC from "@/components/common/TAndC.vue";

enum availableErrorCodeMap {
  generic = "generic",
  configOff = "config-off",
  frequentLimit = "frequent-limit",
  noBank = "no-bank",
  salaried = "salaried",
  noContact = "no-contact"
}

export default defineComponent({
  name: 'Intro',
  components: {TAndC, modal},
  props: {
    msg: String
  },

  data() {
    return {
      showNextPayDay: false,
      showModal: false,
      // isFeatureAvailable: true
      unAvailableReason: '',
      checkingFeature: true
    }
  },

  setup(props, context) {
    const showNextPayDay = true;
    const nextPayDay = '10/10/2020'
    const isFeatureAvailable = ref(true);

    console.log('msg', props.msg)
    return {showNextPayDay, isFeatureAvailable}
  },
  mounted() {
    this.checkFeatureAvailable();
  },
  methods: {
    newRequest() {
      this.$router.push('/request')
    },
    checkFeatureAvailable() {
      this.checkingFeature = true;
      Remote.IsFeatureAvailable().then(res => {
        console.log('res', res)
        if (res.Response.IsFeatureAvailable === false) {
          this.isFeatureAvailable = false
          this.unAvailableReason = this.getErrorMessageFromCode(res.Response.ErrorCode)
        } else {
          this.isFeatureAvailable = true
        }

      })
      .finally(() => {
        this.checkingFeature = false;
      })
    },
    getErrorMessageFromCode(errorCode: string): string {
      //unAvailableCode
      switch (errorCode) {
        case availableErrorCodeMap.generic:
          return `Sorry - Pay Advance is currently unavailable.`;
        case availableErrorCodeMap.configOff:
          return `Pay Advance has been switched off - please contact your company administrators`;
        case availableErrorCodeMap.frequentLimit:
          return `You can only request a Pay Advance once per pay cycle. You've requested this last on <date_of_last_request>. The next time you can request Pay Advance is <date_of_next_pay>`;
        case availableErrorCodeMap.noBank:
          return `None of your bank accounts have been paid into yet. For security, at least one of your bank accounts needs to have been paid into at least once before you can use this feature. Your next pay cycle will occur on <date_of_next_pay>`;
        case availableErrorCodeMap.salaried:
          return `Pay advance is currently unavailable because you're currently already paid in advance as a salaried employee. The next time you can use this feature after <date_of_next_pay>`;
        case availableErrorCodeMap.noContact:
          return `You do not have an email or a mobile number entered into the system. For security, at least one of those needs to be added before you can use this feature.`;
        default:
          return `Sorry - Pay Advance is currently unavailable.`;
      }
    }
  }
})
