<template>
  <transition name="modal">
<!--    <div v-if="show">-->
      <div v-if="show" class="modal-mask">
        <div class="modal-wrapper" >
          <div class="modal-backdrop" @click="clickWrapper"></div>
          <div class="modal-container">
            <div class="modal-header">
              <slot name="header">
                default header
              </slot>
            </div>

            <div class="modal-body">
              <slot name="body">
                default body
              </slot>
            </div>

            <div class="modal-footer">
              <slot name="footer">
                default footer
                <button class="modal-default-button" @click="$emit('close'); show=false">
                  OK
                </button>
              </slot>
            </div>
          </div>
        </div>
      </div>
<!--    </div>-->
  </transition>
</template>

<script>
export default {
  name: "modal",
  data() {
    return {
      show: false
    }
  },
  methods: {
    toggleShow() {
      console.log('toggle clicked')
      this.show = !this.show
    },
    clickWrapper() {
      this.show  = false;
    }
  }
}
</script>

<style scoped>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /*background-color: rgba(0, 0, 0, 0.5);*/
  display: table;
  transition: opacity 0.3s ease;
}

.modal-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  position: absolute;
  width: 940px;
  top: 100px;
  bottom: 60px;
  left: calc(50% - 940px /2);
  margin: 0px auto;
  padding: 30px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
  overflow: auto;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-body {
  margin: 20px 0;
}

.modal-default-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter-active {
  transition: all .3s ease-out;
}

.modal-leave-active {
  transition: all .3s ease-out;
}

.modal-enter-from,
.modal-leave-to {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  opacity: 0;
}

</style>