let GetIsFirstTimeUser = function () {
    // User/History?employeeNo=1&companyNo=2&page=1&size=12
    // return Axios.get('api/User/History', {
    return Axios.get('api/User/IsFirstTimeUser', {
        params: {
            employeeNo: Affinity.employeeNumber,
            companyNo: Affinity.companyNumber,
            page: 1,
            size: 12
        }
    }).then(res => {
        if (typeof res.data === 'string') {
            return JSON.parse(res.data)
        }

        return res.data
    })
};

