// GET /api/User/RequestApproval
let RequestApproval = function (requestAmount: number) {
    return Axios.get('api/User/RequestApproval', {
        params: {
            'request.employeeNo': Affinity.employeeNumber,
            'request.companyNumber': Affinity.companyNumber,
            'request.amountRequested': requestAmount
        }
    }).then(res => {
        if (typeof res.data === 'string') {
            return JSON.parse(res.data)
        }

        return res.data
    })
};

// POST /api/User/RequestPayAdvance
let RequestPayAdvance = function (data: any) {
    return Axios.post('api/User/RequestPayAdvance',{
        "CurrentBankSelection": data.selectedBank,
        "EmployeeNo": Affinity.employeeNumber,
        "CompanyNumber": Affinity.companyNumber,
        "AmountRequested": data.amountRequested
    }).then(res => {
        if (typeof res.data === 'string') {
            return JSON.parse(res.data)
        }

        return res.data
    })
};


