// POST /api/Config/Post
let CreateGroupConfig = async function (data: any) {
    return Axios.post('api/Config/Post', {
        GroupId: data.Id,
        Enabled: data.enabled,
        TimesPerCycle: 1,
        SelectedCycle: '1',
        PayPercentage: data.percentage,
    }, {
        params: {
            companyNo: Affinity.companyNumber,
        }
    }).then(res => {
        if (typeof res.data === 'string') {
            return JSON.parse(res.data)
        }

        return res.data
    })
}

// PUT /api/Config/Put
let UpdateGroupConfig = async function (data: any) {
    return Axios.put('api/Config/Put', {
        GroupId: data.Id,
        Enabled: data.enabled,
        TimesPerCycle: 1,
        SelectedCycle: '1',
        PayPercentage: data.percentage,
    },{
        params: {
            companyNo: Affinity.companyNumber,
        },

    }).then(res => {
        if (typeof res.data === 'string') {
            return JSON.parse(res.data)
        }

        return res.data
    })
}

// GET /api/Config/Get
let GetGroupConfig = async function (groupName: string) {
    return Axios.get('api/Config/Get', {
        params: {
            groupName: groupName,
            companyNo: Affinity.companyNumber,
        }
    }).then(res => {
        if (typeof res.data === 'string') {
            return JSON.parse(res.data)
        }

        return res.data
    })
}

